.profile {
    position: relative;

    &__anchors {
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        z-index: 110;
        display: none;
        overflow-x: auto;
        background: #1a1a1a;
        box-shadow: 0 20px 10px rgba(0, 0, 0, 0.3);
        -webkit-overflow-scrolling: touch;

        &.on {
            display: block;
        }

        nav {
            display: flex;
            overflow-x: auto;
            white-space: nowrap;

            a {
                display: block;
                padding: 0.5em 0.75em;
                transition: all 0.2s;
                color: rgba(255, 255, 255, 0.5);
            }
        }
    }

    &__bage {
        position: absolute;
        top: 30px;
        left: -7px;
        z-index: 90;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;

        @include sm-block() {
            left: 0px;
        }

        &-item {
            position: relative;
            height: 34px;
            padding: 5px 10px;
            background-color: #313131;
            text-align: right;
            text-transform: uppercase;
            cursor: default;

            &::before {
                content: '';
                position: absolute;
                bottom: 100%;
                left: 0;
                width: 0;
                height: 0;
                border-width: 0 0 4px 7px;
                border-style: solid;
                border-left-color: transparent transparent #313131;

                @include xs-block() {
                    display: none;
                }
            }

            &.pornstar {
                display: flex;
                align-items: center;
                justify-content: center;
                background-color: #804a95;

                &::before {
                    border-color: transparent transparent #653478;
                }

                .icon {
                    width: 16px;
                    height: 16px;
                    margin-top: -4px;
                    margin-left: 5px;
                    background-image: url('/assets/files/icons/svg/icon--stars-white.svg');
                    background-size: cover;
                }
            }

            &.new {
                background-color: #f33e3e;

                &::before {
                    border-color: transparent transparent #c0272d;
                }
            }

            &.top {
                background-color: #5cb2f2;

                &::before {
                    border-color: transparent transparent #3e8cc6;
                }
            }

            &.vip {
                background-color: #f3853d;

                &::before {
                    border-color: transparent transparent #cb6b2d;
                }
            }

            span {
                color: #fff;
                font-family: $f-extrabold;
                font-size: 19px;
                line-height: 19px;
            }

            & + .profile__bage-item {
                margin-top: 4px;
            }
        }
    }

    &__online {
        position: absolute;
        top: 24px;
        right: 24px;
        z-index: 100;
        display: flex;
        align-items: center;

        .status {
            width: 10px;
            height: 10px;
            margin-right: 5px;
            border-radius: 5px;
            background-color: #1dcb55;
        }

        span {
            color: #1dcb55;
            font-family: $f-medium;
            font-size: 14px;
        }
    }

    &__meta {
        position: absolute;
        bottom: 30px;
        left: 15px;
        z-index: 10;
    }

    &__realphoto {
        position: relative;
        display: flex;

        &-title {
            padding: 6px;
            border-radius: 3px 0px 0px 3px;
            background: rgba(255, 255, 255, 0.3);
            color: #fff;
            font-size: 14px;
            line-height: 17px;
        }

        &-count {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 6px;
            border-radius: 0px 3px 3px 0px;
            background: rgba(0, 0, 0, 0.2);
            color: #fff;
            font-size: 10px;
            line-height: 12px;

            ins {
                margin-right: 3px;
                text-decoration: none;
            }
        }
    }

    &__name {
        margin-top: 12px;
        font-size: 36px;
        line-height: 43px;

        @include xs-block() {
            width: 100%;
            max-width: 85%;
        }
    }

    &__city {
        margin-top: 10px;
        color: rgba(255, 255, 255, 0.5);
        font-family: $f-light;
        font-size: 16px;
        line-height: 16px;
    }

    &__btn {
        position: relative;

        &-row {
            display: flex;
            flex-wrap: wrap;
            align-items: center;

            @include md-block() {
                justify-content: center;
            }
        }

        &-col {
            display: flex;
            align-items: center;

            @include sm-block() {
                width: 100%;
                margin-bottom: 15px;
            }

            & + .profile__btn-col {
                margin-left: 10px;

                @include sm-block() {
                    margin-left: 0;
                }
            }
        }
    }

    &__quote {
        position: relative;
        z-index: 10;
        width: 100%;
        margin-top: 25px;
        padding: 15px 15px 20px;
        border-radius: 3px;
        background: $color-accent;

        &-p {
            color: rgba(0, 0, 0, 1);

            @include md-block() {
                margin-bottom: 15px;
            }
        }

        &-info {
            position: absolute;
            right: 15px;
            bottom: 10px;
            color: rgba(0, 0, 0, 0.5);
            font-size: 12px;
            line-height: 150%;
        }
    }

    &__info {
        position: relative;

        &-row {
            display: flex;
            flex-wrap: wrap;
            align-items: stretch;
            margin-right: -15px;
            margin-left: -15px;

            @include xs-block() {
                flex-direction: column;
            }
        }

        &-box {
            width: calc(50% - 30px);
            margin-right: 15px;
            margin-left: 15px;
            padding: 30px;
            border-radius: 10px;
            background: #15151e;

            @include sm-block() {
                width: calc(100% - 30px);
            }

            &:first-child {
                @include sm-block() {
                    margin-bottom: 30px;
                }
            }

            .profile__section-title {
                margin: 0 !important;
            }
        }

        &-desc {
            position: relative;
            margin-top: 20px;

            ul {
                column-count: 3;

                @include sm-block() {
                    column-count: 2;
                }

                li {
                    display: flex;
                    flex-direction: column;
                    margin-bottom: 15px;

                    small {
                        margin-bottom: 5px;
                        color: rgba(255, 255, 255, 0.7);
                        font-family: $f-light;
                        font-size: 14px;
                        line-height: 17px;
                    }

                    span {
                        color: #fff;
                        font-family: $f-bold;
                        font-size: 16px;
                        line-height: 19px;
                    }
                }
            }

            p {
                color: rgba(255, 255, 255, 0.7);
                font-size: 18px;
                line-height: 150%;
            }
        }
    }

    &__price {
        position: relative;
        margin-top: 30px;
        margin-bottom: 30px;

        &-row {
            display: flex;
            align-items: center;

            @include sm-block() {
                flex-direction: column;
                align-items: flex-start;
            }
        }

        &-item {
            position: relative;

            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

            margin: 0 0 0 25px;
            padding-right: 25px;
            border-right: 1px solid rgba(255, 255, 255, 0.1);

            &:last-child {
                border-right: 0;
            }

            span {
                margin-bottom: 5px;
                color: rgba(255, 255, 255, 0.7);
                font-family: $f-light;
                font-size: 14px;
                line-height: 17px;
            }

            strong {
                font-family: $f-bold;
                font-size: 16px;
                font-weight: normal;
                line-height: 19px;
            }
        }

        &-title {
            margin-right: 15px;
            color: #fff;
            font-family: $f-medium;
            font-size: 24px;
            line-height: 29px;
        }
    }

    &__usser-data {
        display: flex;
        color: rgba(255, 255, 255, 0.3);
        font-size: 14px;
        line-height: 17px;

        @include lg-block() {
            margin-top: 30px;
        }

        &-view {
            margin-right: 20px;
            padding-right: 20px;
            border-right: 1px solid rgba(255, 255, 255, 0.1);
        }

        strong {
            font-family: $f-bold;
            font-weight: normal;
        }
    }

    &__section {
        position: relative;

        &-row {
            display: flex;
            flex-wrap: wrap;
            align-items: center;

            @include lg-block() {
                flex-direction: column;
            }

            &.justify-content {
                justify-content: space-between;

                @include sm-block() {
                    justify-content: center;
                }
            }
        }

        &-col-2 {
            position: relative;

            &:first-child {
                @include lg-block() {
                    order: 2;
                }
            }
        }

        &-title {
            display: flex;
            align-items: center;
            margin: 50px 0 30px;
            color: #fff;
            font-family: $f-medium;
            font-size: 24px;
            line-height: 29px;

            ins {
                text-decoration: none;
            }

            small {
                opacity: 0.5;
                margin-left: 15px;
                transition: $trs;
                color: #fff;
                font-size: 14px;
                line-height: 17px;
                cursor: pointer;

                &:hover {
                    opacity: 1;
                    color: $color-accent;
                }
            }
        }
    }

    &__video {
        position: relative;

        @include xs-block() {
            padding-top: 20px;
        }

        &-thumb {
            position: relative;
            overflow: hidden;
            border-radius: 10px;
            cursor: pointer;

            &::before {
                content: '';
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                background: linear-gradient(
                    180deg,
                    rgba(21, 21, 30, 0.8) 0%,
                    rgba(21, 21, 30, 0.4) 98.43%
                );
            }

            &:hover .profile__video-play {
                opacity: 1;
            }
        }

        &-wrap {
            position: absolute;
            top: 50%;
            left: 50%;
            display: flex;
            align-items: center;
            transform: translate(-50%, -50%);

            @include sm-block() {
                justify-content: center;
                width: 100%;
                transform: translate(-50%, -50%);
            }
        }

        &-title {
            margin-right: 50px;
            text-align: center;

            @include xs-block() {
                margin-right: 20px;
            }

            span {
                display: block;
                color: #fff;
                font-size: 24px;
                line-height: 29px;

                @include xs-block() {
                    font-size: 18px;
                }

                @include i5-block() {
                    font-size: 16px;
                }
            }

            small {
                color: #fff;
                font-size: 14px;
                line-height: 17px;
            }
        }

        &-play {
            opacity: 0.8;
            width: 120px;
            height: 120px;
            transition: all 0.3s ease;
            background-image: url('/assets/files/icons/svg/icon--play.svg');
            background-size: cover;

            @include xs-block() {
                width: 72px;
                height: 72px;
            }
            @include i5-block() {
                width: 52px;
                height: 52px;
            }
        }

        &-btn {
            position: absolute;
            bottom: 65px;
            left: 50%;
            z-index: 90;
            display: flex;
            align-items: center;
            justify-content: center;
            transform: translateX(-50%);

            @include xs-block() {
                right: 20px;
                bottom: 10px;
                left: inherit;
                transform: translateX(0);
            }

            &:hover .profile__video-btn-play {
                opacity: 1;
            }

            &-play {
                opacity: 0.7;
                width: 52px;
                height: 52px;
                transition: all 0.3s ease;
                background-image: url('/assets/files/icons/svg/icon--play.svg');
                background-size: cover;
                filter: drop-shadow(0px 10px 70px rgba(0, 0, 0, 0.1));

                @include xs-block() {
                    opacity: 1;
                }
            }

            span {
                margin-right: 15px;
                color: #fff;
                font-size: 14px;
                line-height: 17px;

                @include md-block() {
                    display: none;
                }
            }
        }
    }

    &__gallery {
        position: relative;

        &-row {
            display: flex;
            flex-wrap: wrap;
        }

        &-item {
            width: 157px;
            height: 157px;
            margin-top: 1px;
            margin-left: 1px;

            img {
                width: 100%;
                height: 100%;
                border-radius: 10px;
            }
        }
    }

    &__reviews {
        position: relative;
        margin-top: 50px;
        margin-bottom: 80px;

        h2 {
            margin-bottom: 30px;
            font-family: $f-bold;
            font-weight: normal;
            text-transform: unset;
        }
    }

    &-slider {
        position: relative;

        &-container {
            width: 100%;
            max-width: 1140px;
            margin-right: auto;
            margin-left: auto;
            overflow: visible;
        }

        .profile-slide {
            position: relative;

            &::before {
                content: '';
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                background: linear-gradient(
                    180deg,
                    rgba(0, 0, 0, 0) 34.38%,
                    #000 100%
                );
            }
        }
    }

    .swiper-btn-next,
    .swiper-btn-prev {
        @include md-block() {
            display: none;
        }
    }

    .btn {
        padding: 15px 28px;

        @include lg-block() {
            padding: 15px 20px;
        }

        @include sm-block() {
            padding: 15px 16px;
        }
    }
}
